import "./Post.css";

const Post = (props) => {
    return (
        <div className={"post " + props.type}>
            <img
                src={props.imgSrc}
                alt=""
                className="post-img"
            />
            <div className="post-details">
                <div className="heading">
                    {props.heading}
                </div>
                <div className="short-desc">
                    {props.desc}
                </div>

                <div className="post-by">
                    <div className="author">{props.author}</div>
                    <div className="time-ago">{props.time}</div>
                </div>
            </div>  
        </div>
    );
};

export default Post;
